.popup{
    background: rgb(0, 0, 0, 0.5);
    position: absolute;
    top:0%;
    left:0%;
    width: 100vw;
    
    transition: all 1500ms ease-in-out;
    height: 100vh;
    z-index: 1;
}

.popup-item{
    position: absolute;
    top:20%;
    left:40%;
    height:auto;
    min-width: 30vw;
    background-color:rgb(255, 255, 255);
    border-radius:5px;
    z-index: 10;
    transition-timing-function: ease-in-out;
    transition: all .3s;
   
}

.modal_title{
    font-size: 2rem;
    margin-top: .5rem;
    color: black
}

